.slider__slide:after { position: absolute; top: 0; left: 0; width: 100%; height: 100%; content: ''; z-index: 1; background-color: rgba(20, 20, 20, 0.5); opacity: 0; visibility: hidden; transition: opacity 0.4s, visibility 0.4s; }

